// requires global variable db, declared in App.svelte
// each item is a document in pouchdb
var List, ListItem, PouchDoc;

ListItem = require("./ListItem.js");

PouchDoc = require("../PouchDb/pouch_doc.js");

List = (function() {
  class List extends PouchDoc {
    // attributes starting with _ are managed and need to be updated every time a change has been made

    // array version of list items

    // this is not a function because of Svelte

    // convert list_items from an object to an array -- from pouch
    after_construct(model) {
      var that;
      that = this;
      // if passed an object, assign it to the current item
      //console.error "NEW ROOT ITEM CONSTRUCT"
      //console.log model
      this["list_items"] = [];
      this["list_items_raw"] = [];
      if (model != null) {
        model["doctype"] = "List";
        return this.depouchify(model);
      }
    }

    setRaw(o) {
      return this.list_items_raw = o;
    }

    default_value_for(key) {
      var output;
      output = null;
      switch (key) {
        case "id":
        case "_id":
          output = uuid();
          break;
        case "is_expanded":
          output = {
            list: true,
            radar: true
          };
          break;
        case "is_ongoing":
        case "is_archived":
        case "is_favorite":
          output = false;
          break;
        case "progress":
          output = {
            percent_complete: 0,
            color: "light-gray"
          };
          break;
        case "index":
        case "model_version":
          output = 0;
          break;
        case "list_items":
        case "labels":
        case "ancestry":
          output = [];
          break;
        case "settings":
          output = {
            // importable from other projects
            is_expanded: {
              list: true,
              radar: true
            },
            user_defined_data: [{}],
            // importable from other projects
            labels: [
              {
                value: "unique",
                foreground: "",
                background: "",
                icon: "" // structure tbd
              }
            ]
          };
      }
      //...
      return output;
    }

    migrate(outputRef) {
      var that;
      that = this;
      if (this.model_version === 0) {
        that.id = uuid(); // new id
        this.list_items.forEach(function(listItem) {
          return listItem.migrate(outputRef, that.id);
        });
        this["is_expanded"] = {
          list: this["is_expanded"],
          radar: this["is_expanded_radar"]
        };
        return outputRef.append(Object.reject(that, ["list_items", "is_expanded_radar"]));
      }
    }

    // after migrate, clear the list_items array
    override(model) {}

    // update mirrored items
    refresh() {}

    // @list_items = @@list_items
    stub(override) {
      var base, newId;
      base = this.model();
      newId = uuid();
      base._id = newId;
      base.id = newId;
      base.name = `New ${this.type} created at ${Date.create().format('%Y-%m-%d %H:%M:%S')}`;
      base.created_at = Date.create();
      base.updated_at = Date.create();
      if (override != null) {
        return Object.merge(base, override, true);
      } else {
        return base;
      }
    }

    /*

    RADAR

    */
    /*

    LIST ITEM HANDLING

    */
    // add from click on divider
    insert_item(item, scb, ecb) {
      var newListItem, parent;
      item["list_id"] = this.id;
      
      // the passed item from the event is not a ListItem instance
      newListItem = new ListItem(item);
      this.list_items_raw.append(newListItem);
      if (newListItem.ancestry.length === 0) {
        this.list_items.append(newListItem);
      } else {
        parent = this.list_items_raw.find({
          id: newListItem.ancestry.last()
        });
        if (parent != null) {
          parent.list_items.append(newListItem);
        }
      }
      return newListItem.save(scb);
    }

    /*

     _     _                         _           
    | |__ (_) ___ _ __ __ _ _ __ ___| |__  _   _ 
    | '_ \| |/ _ \ '__/ _` | '__/ __| '_ \| | | |
    | | | | |  __/ | | (_| | | | (__| | | | |_| |
    |_| |_|_|\___|_|  \__,_|_|  \___|_| |_|\__, |
                                       |___/ 

     */
    get_children(item, scb, ecb) {
      return db.find({
        selector: {
          ancestry: {
            $all: [item.id]
          }
        }
      }).then(function(result) {
        if (scb != null) {
          return scb(result.docs);
        }
      });
    }

    get_siblings(item, scb, ecb) {
      return db.find({
        selector: {
          ancestry: {
            $eq: item.ancestry
          }
        }
      }).then(function(result) {
        if (scb != null) {
          return scb(result.docs);
        }
      });
    }

    /*

            _     _ _                 
           | |   (_) |                
    _ __   ___  ___| |_   _| |_ ___ _ __ ___  
     | '_ \ / _ \/ __| __| | | __/ _ \ '_ ` _ \ 
     | | | |  __/\__ \ |_  | | ||  __/ | | | | |
     |_| |_|\___||___/\__| |_|\__\___|_| |_| |_|
             ______                   
            |______|                  

     */
    nest_item(origin, destination, scb, ecb) {
      var bulkChangeIds, changes, get_children, itemsToChange, newAncestry, newIndex, siblingsMaxIndex, that;
      console.error("List.nest_item");
      that = this;
      get_children = this.get_children; // to avoid reference issues
      newAncestry = destination.ancestry.clone().append(destination.id);
      newIndex = 0;
      // getChildren
      siblingsMaxIndex = 0;
      itemsToChange = [];
      changes = {};
      itemsToChange = that.list_items_raw.filter(function(o) {
        return (o.id === origin.id) || (o.ancestry.includes(origin.id));
      });
      console.error("itemsToChange");
      console.debug(itemsToChange);
      itemsToChange.forEach(function(item) {
        if (item.id === origin.id) { // parent item
          return changes[item.id] = {
            id: item.id,
            ancestry: newAncestry.clone()
          };
        } else {
          return changes[item.id] = {
            id: item.id,
            ancestry: newAncestry.clone().append(origin.id).append(item.ancestry.from(item.ancestry.findIndex(origin.id) + 1))
          };
        }
      });
      bulkChangeIds = itemsToChange.map(function(item) {
        return item.id;
      });
      return db.find({
        selector: {
          id: {
            $in: bulkChangeIds
          }
        }
      }).then(function(result) {
        var changeQueue;
        // console.log "---------------------------------"
        //console.error "docs to change"
        //console.debug result.docs
        changeQueue = result.docs.map(function(dbDoc) {
          var changesForThisDoc;
          changesForThisDoc = changes[dbDoc.id];
          return Object.merge(dbDoc, changesForThisDoc);
        });
        //console.error "CHANGEQUEUE"
        console.table(changeQueue.map(function(i) {
          return {
            id: i.id,
            name: i.name,
            ancestry: JSON.stringify(i.ancestry),
            _rev: i._rev
          };
        }));
        return db.bulkDocs(changeQueue).then(function(result) {
          console.error("nest_item completd");
          console.log(result);
          if (scb != null) {
            return scb(result);
          }
        });
      });
    }

    sync() {
      console.log("pouchresourece.sync");
      return window.pouchSync();
    }

    //-----------------------------------------------

    // db

    //-----------------------------------------------
    saveable_model() {
      var output;
      // todo: make sure system fields not saved
      output = Object.select(this, this.attribute_whitelist);
      // output.list_items = @pouchify_list_items
      return output;
    }

    /*

    _______ _____  ______ ______ _____ ________     __
     |__   __|  __ \|  ____|  ____|_   _|  ____\ \   / /
    | |  | |__) | |__  | |__    | | | |__   \ \_/ / 
    | |  |  _  /|  __| |  __|   | | |  __|   \   /  
    | |  | | \ \| |____| |____ _| |_| |       | |   
    |_|  |_|  \_\______|______|_____|_|       |_|   

     */
    treeify() {
      // console.error "List treeify"
      this.list_items = this.list_items_raw.filter(function(item) {
        return item.ancestry.length === 0;
      });
      window.currentList = this;
      return this.list_items.forEach(function(item) {
        return item.treeify();
      });
    }

    housekeeping() {}

    // fix sorting etc
    depouchify(model) {
      var that;
      // get the document, map the attributes to this class instance
      // recurse through the list_items attributes and convert
      // the list_items to ListItem classes
      // console.log "depouchify"
      that = this;
      return this.attribute_whitelist.forEach(function(key) {
        // check that model has key
        if (Object.has(model, key)) {
          switch (key) {
            case "list_items":
            case "list_items_raw":
              console.log("erase list items");
              return that[key] = [];
            case "is_expanded":
              if (Object.isBoolean(model[key])) {
                return that[key] = {
                  list: model[key],
                  radar: true
                };
              } else {
                return that[key] = model[key];
              }
              break;
            default:
              return that[key] = model[key];
          }
        } else {
          return that[key] = that.default_value_for(key);
        }
      });
    }

    pouchify() {
      var that, writeable;
      // de-class this instance for pouch
      // map the root attributes
      // iterate through the list_items and use the saveable model
      that = this;
      writeable = {};
      that.attribute_whitelist.forEach(function(key) {
        // console.warn "key: #{key} value: #{that[key]}"
        return writeable[key] = that[key];
      });
      // todo: fill in any gaps that might be missing here
      writeable["_id"] = that.id;
      //console.error "pouchify writeable"
      //console.debug writeable
      return writeable;
    }

    /*
       _                  
     ___| | ___  _ __   ___ 
    / __| |/ _ \| '_ \ / _ \
     | (__| | (_) | | | |  __/
    \___|_|\___/|_| |_|\___|

     */
    clone(thenCb) {
      var sourceSelector, that;
      that = this;
      sourceSelector = {
        $or: [
          {
            id: that.id
          },
          {
            list_id: that.id
          }
        ]
      };
      return db.find({
        selector: sourceSelector
      }).then(function(result) {
        var clonedDocs, newKeys, newList, newListId, originalList, originalListItems;
        originalList = result.docs.find(function(o) {
          return o.doctype === "List";
        });
        originalListItems = result.docs.filter(function(o) {
          return o.doctype === "ListItem";
        });
        // set the index depending on whether it's current or archived
        newKeys = {};
        clonedDocs = [];
        //console.log originalList
        delete originalList.doctype;
        newList = new List(originalList);
        newListId = uuid();
        newList["id"] = newListId;
        newList["_id"] = newListId;
        newList.name = `${newList.name} copy`;
        //console.error 'NEW LIST'
        //console.log newList.pouchify()
        clonedDocs.append(newList.pouchify());
        originalListItems.sortBy(function(doc) {
          return doc.ancestry.length;
        });
        originalListItems.forEach(function(doc) {
          var newListItem;
          newKeys[doc.id] = uuid();
          newListItem = new ListItem(doc);
          newListItem.list_id = newListId;
          newListItem.id = newKeys[doc.id];
          newListItem._id = newKeys[doc.id];
          newListItem.ancestry = doc.ancestry.map(function(ancestor) {
            if (newKeys[ancestor]) {
              return newKeys[ancestor];
            } else {
              return ancestor;
            }
          });
          return clonedDocs.append(newListItem.pouchify());
        });
        
        //console.error "clonedDocs"
        //console.table clonedDocs
        return db.bulkDocs(clonedDocs).then(function(result) {
          //console.log "bulk docs result"
          //console.debug result
          if (thenCb != null) {
            return thenCb(newListId); // for redirecting
          }
        }).catch(function(err) {
          console.log("bulk insert error");
          return console.log(err);
        }).finally(function() {
          return console.log("finally");
        });
      }).catch(function(err) {
        if (typeof catchCb !== "undefined" && catchCb !== null) {
          return catchCb(err);
        } else {
          console.log("select  err");
          return console.log(err);
        }
      }).finally(function() {
        if (typeof finallyCb !== "undefined" && finallyCb !== null) {
          return finallyCb();
        }
      });
    }

    get(id, thenCb, catchCb, finallyCb) {
      var tcb, that;
      console.log(`Fetch ${id}`);
      that = this;
      tcb = function(doc) {
        that.depouchify(doc);
        //console.log "tbc"
        //console.log doc
        // get children
        return db.find({
          selector: {
            doctype: "ListItem",
            list_id: id
          }
        
        //ancestry:
        //  $eq: []
        }).then(function(result) {
          //console.log "ListItems Found: #{result.docs.length}"
          //console.table result.docs
          //treeified = that.treeify(id, result.docs, true)
          //console.error "TREEIFIED"
          //console.log JSON.stringify(treeified, null, 2)
          //that.list_items = treeified
          //result.docs.forEach (doc)->
          that.list_items_raw = result.docs.map(function(doc) {
            var listItem;
            listItem = new ListItem(doc);
            listItem.list_reference = that;
            return listItem;
          });
          console.debug(that);
          if (thenCb != null) {
            return thenCb(that);
          }
        });
      };
      return this.selectOne(id, tcb, catchCb, finallyCb);
    }

    patchChange(change, thenCb) {
      var that;
      that = this;
      console.error(`patchChange for [${this.id}] ${this.name}`);
      // update the attributes on the object
      Object.keys(change).forEach(function(key) {
        return that[key] = change[key];
      });
      change["id"] = this.id;
      return db.get(this.id).then(function(originalDoc) {
        Object.merge(originalDoc, change);
        originalDoc.updated_at = Date.create();
        return db.put(originalDoc).then(function(response) {
          console.log("update response");
          console.log(response);
          if (thenCb != null) {
            return thenCb(response);
          }
        }).catch(function(err) {
          if (typeof catchCb !== "undefined" && catchCb !== null) {
            return catchCb(err);
          } else {
            console.log("patch.catch");
            return console.log(err);
          }
        }).finally(function() {
          // that.sync()
          if (typeof finallyCb !== "undefined" && finallyCb !== null) {
            return finallyCb();
          }
        });
      });
    }

    update(thenCb, catchCb, finallyCb) {
      var changedDoc, that;
      that = this;
      changedDoc = this.pouchify();
      return db.get(changedDoc.id).then(function(originalDoc) {
        changedDoc["_id"] = changedDoc.id;
        changedDoc["_rev"] = originalDoc._rev;
        // Object.merge
        changedDoc.updated_at = Date.create();
        return db.put(changedDoc).then(function(response) {
          console.log("--------------root item updated");
          if (thenCb != null) {
            return thenCb(response);
          }
        }).catch(function(err) {
          if (catchCb != null) {
            return catchCb(err);
          }
        });
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.error("List.update");
          return console.debug(err);
        }
      }).finally(function() {
        window.pouchSync();
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

    delete(cb) {
      var that;
      that = this;
      // remove this and its children
      return db.find({
        selector: {
          $or: [
            {
              list_id: that.id
            },
            {
              id: that.id
            }
          ]
        }
      }).then(function(result) {
        console.error("Deletion queue");
        console.log(result.docs);
        return db.bulkDocs(result.docs.map(function(doc) {
          doc["_deleted"] = true;
          return doc;
        })).then(function(result) {
          console.error("Lists deleted");
          if (cb != null) {
            return cb(result);
          }
        });
      });
    }

  };

  List.prototype.doctype = "List";

  List.prototype.attribute_whitelist = [
    "id",
    "model_version",
    "doctype",
    "name",
    "description",
    "progress",
    "index",
    "level",
    "is_favorite", // can include archived
    "is_expanded",
    "is_archived",
    "due_date",
    "labels",
    "settings",
    "created_at",
    "updated_at"
  ];

  return List;

}).call(this);

module.exports = List;
