var ListItem, PouchDoc, uuid;

uuid = require("uuid");

PouchDoc = require("../PouchDb/pouch_doc.js");

ListItem = (function() {
  class ListItem extends PouchDoc {
    after_construct(model) {
      // console.log "after construct"
      // if passed an object, assign it to the current item
      if (model != null) {
        model["doctype"] = "ListItem"; // force it      
        // if model.model_version?
        this.depouchify(model);
      }
      this["list_reference"] = void 0;
      this["parent_reference"] = void 0; // null if it's a list
      this["lineage"] = void 0;
      this["depth"] = void 0;
      return this["list_items"] = [];
    }

    migrate(outputRef, list_id) {
      // console.log "  list_item migrate: #{@name}"
      this.list_items.forEach(function(listItem) {
        return listItem.migrate(outputRef, list_id);
      });
      // then do this one
      //console.log "migrate this to new model"
      this["_id"] = this.uuid;
      this["id"] = this.uuid;
      this["list_id"] = list_id;
      return outputRef.append(Object.reject(this, "list_items"));
    }

    // console.debug @

    // update mirrored items
    refresh() {}

    // refresh?

    // attributes starting with _ are managed and need to be updated every time a change has been made

    // array version of list items
    default_value_for(key) {
      var output;
      output = null;
      switch (key) {
        case "id":
        case "_id":
          output = uuid();
          break;
        case "uuid":
          output = uuid();
          break;
        case "is_expanded":
          output = {
            list: true,
            radar: true
          };
          break;
        case "is_ongoing":
        case "is_hidden":
          output = false;
          break;
        case "progress":
          output = {
            percent_complete: 0,
            color: "light-gray"
          };
          break;
        case "index":
        case "model_version":
          output = 0;
          break;
        case "labels":
        case "ancestry":
          output = [];
      }
      return output;
    }

    /*

      _     _                         _           
     | |__ (_) ___ _ __ __ _ _ __ ___| |__  _   _ 
     | '_ \| |/ _ \ '__/ _` | '__/ __| '_ \| | | |
     | | | | |  __/ | | (_| | | | (__| | | | |_| |
     |_| |_|_|\___|_|  \__,_|_|  \___|_| |_|\__, |
                                            |___/ 

     */
    child_ancestry() {
      return this.ancestry.clone().append(this.id);
    }

    /*

    _                 _  __       
     | |_ _ __ ___  ___(_)/ _|_   _ 
     | __| '__/ _ \/ _ \ | |_| | | |
     | |_| | |  __/  __/ |  _| |_| |
    \__|_|  \___|\___|_|_|  \__, |
                          |___/ 

     */
    treeify() {
      var that;
      that = this;
      //console.log "ListItem.treeify"
      //console.log @
      this.list_items = this.list_reference.list_items_raw.filter(function(o) {
        return that.id === o.ancestry.last();
      });
      // fix
      this.list_items.forEach(function(o) {
        var fixScb;
        o.parent_reference = that;
        if (!that.child_ancestry().isEqual(o.ancestry)) {
          console.error(`item ${o.name} has bad ancestry.. should be [${that.child_ancestry()}] is [${o.ancestry.join(",")}]`);
          //o.ancestry = that.child_ancestry()
          fixScb = function(result) {
            return console.warn(`Fixed ancestry of [${o.id}] ${o.name} in db`);
          };
          return o.patchChange({
            ancestry: that.child_ancestry()
          }, fixScb);
        }
      });
      return this.list_items.forEach(function(child) {
        return child.treeify();
      });
    }

    /*
               _   
              | |  
     _ __   ___  ___| |_ 
    | '_ \ / _ \/ __| __|
    | | | |  __/\__ \ |_ 
    |_| |_|\___||___/\__|

     */
    nest(item) {
      var descendant, index, itemToAppend;
      if (item.ancestry.last() === this.id) {
        if (ListItem.nest != null) {
          itemToAppend = item;
        } else {
          itemToAppend = new ListItem(item);
        }
        return this.list_items.append(itemToAppend); // deeper
      } else {
        index = item.ancestry.findIndex(this.id) + 1;
        
        // find the child item in the current list
        descendant = this.list_items.find({
          id: item.ancestry[index]
        });
        return descendant.nest(item);
      }
    }

    /*

      ____                  _     
     |  _ \ ___  _   _  ___| |__  
     | |_) / _ \| | | |/ __| '_ \ 
     |  __/ (_) | |_| | (__| | | |
     |_|   \___/ \__,_|\___|_| |_|

     */
    depouchify(model) {
      var that;
      //console.log "depouchify list_item #{model.name}"
      //console.log model
      that = this;
      //pouchDocument = {}

      //model = pouchDocument

      // merge into this instance
      //console.log Object.keys(model)
      return that.attribute_whitelist.forEach(function(key) {
        // check that model has key
        if (Object.has(model, key)) {
          switch (key) {
            case "is_expanded":
              if (Object.isBoolean(model[key])) {
                return that[key] = {
                  list: model[key],
                  radar: true
                };
              } else {
                return that[key] = model[key];
              }
              break;
            case "list_items":
              return that[key] = [];
            default:
              return that[key] = model[key];
          }
        } else {
          return that[key] = that.default_value_for(key);
        }
      });
    }

    pouchify() {
      // de-class this instance for pouch
      // map the root attributes
      // iterate through the list_items
      return this.saveable_model();
    }

    saveable_model() {
      var output;
      output = Object.select(this, this.attribute_whitelist);
      if (output._id == null) {
        output._id = output.id;
      }
      return output;
    }

    model() {
      return {
        // if sourceversion < latest version, then some upgrades may be required
        // if null then return latest
        uuid: "",
        model_version: 0,
        // common to root items
        name: "new list item",
        description: "", // optional, not visible, edit in a modal
        progress: {
          percent_complete: 0,
          color: "light-gray"
        },
        index: 0, // integer # ordinal
        ancestry: [],
        is_hidden: false,
        is_expanded: {
          radar: true,
          list: true // collapse state
        },
        is_ongoing: false,
        due_date: null,
        list_items: [],
        labels: [],
        
        // /common
        has_clip: false,
        clip_value: "",
        user_defined_data: {}
      };
    }

    
    // levels are set by children attribute
    sync() {
      //console.log "pouchresourece.sync"
      return window.pouchSync();
    }

    /*

       _                  
     ___| | ___  _ __   ___ 
    / __| |/ _ \| '_ \ / _ \
     | (__| | (_) | | | |  __/
    \___|_|\___/|_| |_|\___|

     */
    clone(options, scb) {
      var that;
      that = this;
      return db.find({
        selector: {
          $or: [
            {
              ancestry: {
                $all: [that.id]
              }
            },
            {
              id: that.id
            }
          ]
        }
      }).then(function(result) {
        var clonedDocs, newKeys;
        console.error("clone queue");
        console.log(result.docs);
        newKeys = {};
        clonedDocs = [];
        result.docs.sortBy(function(doc) {
          return doc.ancestry.length;
        });
        result.docs.forEach(function(doc) {
          var newDoc;
          newKeys[doc.id] = uuid();
          newDoc = new ListItem(doc);
          if (doc.id === that.id) {
            newDoc.name = `${newDoc.name} copy`;
            // new index
            if (options != null) {
              if (options.index != null) {
                newDoc.index = options.index;
              } else {
                options.index = doc.index + Number.random(100, 200);
              }
            }
          }
          newDoc.id = newKeys[doc.id];
          newDoc._id = newKeys[doc.id];
          newDoc.ancestry = doc.ancestry.map(function(ancestor) {
            if (newKeys[ancestor]) {
              return newKeys[ancestor];
            } else {
              return ancestor;
            }
          });
          return clonedDocs.append(newDoc.pouchify());
        });
        console.log("old docs");
        console.debug(result.docs);
        console.log("new docs");
        console.debug(clonedDocs);
        console.log("bulk insert");
        // todo: bulk insert docs
        return db.bulkDocs(clonedDocs).then(function(result) {
          console.log("bulk docs result");
          console.debug(result);
          console.log("scb?");
          console.log(scb);
          if (scb != null) {
            return scb(result);
          }
        }).catch(function(err) {
          console.log("bulk insert error");
          return console.log(err);
        }).finally(function() {
          console.log("finally");
          return that.sync();
        });
      });
    }

    
    // get children irrespective of level
    get_descendants(scb, ecb) {
      var that;
      that = this;
      return db.find({
        selector: {
          ancestry: {
            $all: [that.id]
          }
        }
      }).then(function(result) {
        if (scb != null) {
          return scb(result.docs);
        }
      });
    }

    patchChange(change, thenCb) {
      var that;
      that = this;
      console.error(`patchChange for [${this.id}] ${this.name}`);
      // update the attributes on the object
      Object.keys(change).forEach(function(key) {
        return that[key] = change[key];
      });
      change["id"] = this.id;
      return db.get(this.id).then(function(originalDoc) {
        Object.merge(originalDoc, change);
        originalDoc.updated_at = Date.create();
        return db.put(originalDoc).then(function(response) {
          console.log("update response");
          console.log(response);
          if (thenCb != null) {
            return thenCb(response);
          }
        }).catch(function(err) {
          if (typeof catchCb !== "undefined" && catchCb !== null) {
            return catchCb(err);
          } else {
            console.log("patch.catch");
            return console.log(err);
          }
        }).finally(function() {
          that.sync();
          if (typeof finallyCb !== "undefined" && finallyCb !== null) {
            return finallyCb();
          }
        });
      });
    }

    save(cb) {
      var item, that;
      that = this;
      item = this.pouchify();
      return db.put(item).then(function(result) {
        if (cb != null) {
          return cb(result);
        }
      }).finally(function() {
        return that.sync();
      });
    }

    delete(cb) {
      var that;
      that = this;
      // remove this and its children
      return db.find({
        selector: {
          $or: [
            {
              ancestry: {
                $all: [that.id]
              }
            },
            {
              id: that.id
            }
          ]
        }
      }).then(function(result) {
        console.error("Deletion queue");
        console.log(result.docs);
        return db.bulkDocs(result.docs.map(function(doc) {
          doc["_deleted"] = true;
          return doc;
        })).then(function(result) {
          console.error("Lists deleted");
          if (cb != null) {
            return cb(result);
          }
        });
      }).finally(function() {
        return that.sync();
      });
    }

  };

  ListItem.prototype.model = "List Item";

  ListItem.prototype.attribute_whitelist = ["id", "list_id", "model_version", "doctype", "name", "description", "progress", "index", "ancestry", "is_hidden", "is_expanded", "is_ongoing", "due_date", "labels", "user_defined_data", "created_at", "updated_at"];

  // treeify support
  ListItem.prototype.list_items = [];

  return ListItem;

}).call(this);

module.exports = ListItem;
