var PouchDoc;

PouchDoc = (function() {
  class PouchDoc {
    constructor(model) {
      
      //console.log "BEFORE"
      //console.log @
      this.after_construct(model);
    }

    //console.log "COSNTRUCTOR"
    after_construct(model) {}

    // console.log "POUCHDOC AFTERCONSTRUCT"
    select(selector, thenCb, catchCb, finallyCb) {
      return db.find(selector).then(function(result) {
        if (thenCb != null) {
          // consider removing transformation
          return thenCb(result.docs);
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.error("PouchDoc.select");
          return console.debug(err);
        }
      }).finally(function() {
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

    selectOne(id, thenCb, catchCb, finallyCb) {
      return db.get(id).then(function(doc) {
        if (thenCb != null) {
          // consider removing transformation
          return thenCb(doc);
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.error("PouchDoc.selectOne");
          return console.debug(err);
        }
      }).finally(function() {
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

    patch(changedDoc, thenCb, catchCb, finallyCb) {
      //that = @
      console.error("Patch");
      console.log("change");
      console.log(changedDoc);
      return db.get(changedDoc.id).then(function(originalDoc) {
        Object.merge(originalDoc, changedDoc);
        //console.log "changed doc"
        originalDoc.updated_at = Date.create();
        console.error("patch");
        console.log(originalDoc);
        //console.log originalDoc
        return db.put(originalDoc).then(function(response) {
          if (thenCb != null) {
            return thenCb(response);
          }
        }).catch(function(err) {
          if (catchCb != null) {
            return catchCb(err);
          } else {
            console.log("put.catch");
            return console.log(err);
          }
        }).finally(function() {
          //that.sync()
          if (finallyCb != null) {
            return finallyCb();
          }
        });
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.log("update.catch");
          return console.log(err);
        }
      }).finally(function() {
        //that.sync()
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

    bulkPatch(changedDocs, thenCb, catchCb, finallyCb) {}

    update(thenCb, catchCb, finallyCb) {
      var changedDoc, that;
      that = this;
      changedDoc = this.pouchify();
      return db.get(changedDoc.id).then(function(originalDoc) {
        changedDoc["_id"] = changedDoc.id;
        changedDoc["_rev"] = originalDoc._rev;
        // Object.merge
        changedDoc.updated_at = Date.create();
        return db.put(changedDoc).then(function(response) {
          console.log("--------------root item updated");
          if (thenCb != null) {
            return thenCb(response);
          }
        }).catch(function(err) {
          if (catchCb != null) {
            return catchCb(err);
          }
        });
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.error("List.update");
          return console.debug(err);
        }
      }).finally(function() {
        window.pouchSync();
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

    destroy(id, thenCb, catchCb, finallyCb) {
      var that;
      that = this;
      return db.find({
        selector: {
          "id": id
        }
      }).then(function(response) {
        var refreshedCb, removedCb;
        if (thenCb != null) {
          refreshedCb = function() { // execute then cb
            return thenCb(that.items);
          };
          removedCb = function() { // refresh @ items
            // that.sync()
            // that.select refreshedCb, catchCb, finallyCb
            if (thenCb) {
              return thenCb();
            }
          };
          console.log("response");
          console.log(response);
          // docToRemove = response.docs.first()
          response.docs.forEach(function(doc) {
            return doc["_deleted"] = true;
          });
          
          // db.remove docToRemove, removedCb
          return db.bulkDocs(response.docs).then(removedCb).catch(function(err) {
            if (catchCb != null) {
              return catchCb(err);
            } else {
              console.error("List.destroy");
              return console.debug(err);
            }
          });
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.error("List.destroy");
          return console.debug(err);
        }
      }).finally(function() {
        // that.sync()
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

  };

  PouchDoc.prototype.doctype = "PouchDoc";

  return PouchDoc;

}).call(this);

module.exports = PouchDoc;
