var PouchDb;

PouchDb = (function() {
  class PouchDb {
    stopSync() {
      var that;
      that = this;
      if (that.notifier != null) {
        that.notifier("Sync completed");
      }
      that.is_syncing = false;
      // console.log "stop sync"
      if (that.sync_store != null) {
        that.sync_store.set(false);
      }
      if (that.sync_pending) {
        // console.error "sync pending, resyncing"
        that.sync_pending = false;
        // sync again
        return that.sync(that);
      }
    }

    sync(scope) {
      var delayedEnd, that;
      console.error("PouchDb.sync!");
      this.init();
      //console.log "PouchDb.sync"
      //console.log @remote_db
      if (scope != null) {
        that = scope;
      } else {
        that = this;
      }
      if (this.sync_db) {
        // console.log "current sync status: #{that.sync_db}"
        if (that.is_syncing) {
          // console.error "CURRENTLY SYNCING"
          if (!that.sync_pending) {
            that.sync_pending = true;
          }
          // console.error "already pending, will not re-pend"
          return that.notifier("Currently syncing, will resync after current sync is complete.");
        } else {
          // console.log "Currently syncing, will resync after current sync is complete."
          if (that.notifier != null) {
            that.notifier("Starting sync");
          }
          this.is_syncing = true;
          // console.log @sync_store
          this.sync_store.set(true);
          delayedEnd = function() {
            return that.stopSync();
          };
          if (this.remote_db != null) {
            // console.log "syncing to remote db: #{@is_syncing}"
            localStorage.setItem("radar_last_sync_attempt", Date.create());
            // console.log "localstorage radar_last_sync_attempt #{localStorage.getItem(@last_sync_key)}"
            // console.log Date.create(localStorage.getItem(@last_sync_key))
            return db.sync(this.remote_db, {
              retry: true
            }).on('complete', function() {
              console.log(`PouchDb.complete! ${that.is_syncing}`);
              return delayedEnd.delay(1000);
            }).on('change', function(change) {
              // yo, something changed!
              return console.log("PouchDb.sync change");
            // console.log change
            }).on('paused', function(info) {
              // replication was paused, usually because of a lost connection
              // console.log "sync paused"
              // console.log info
              return delayedEnd.delay(1000);
            // replication was resumed
            // console.log "sync active"
            // console.log info
            }).on('active', function(info) {}).on('error', function(err) {
              // console.log "sync error"
              // console.log err
              return delayedEnd.delay(1000);
            });
          }
        }
      } else {
        // console.warn "Did not sync because sync is disabled."
        return true;
      }
    }

    init() {
      // last_sync = timestsamp
      if (localStorage.getItem(this.sync_key) != null) {
        this.sync_db = localStorage.getItem(this.sync_key) === "true";
        if (localStorage.getItem(this.sync_key) === "true") {
          // console.log "Syncing enabled..."
          if (localStorage.getItem(this.remote_db_key) != null) {
            this.remote_db = localStorage.getItem(this.remote_db_key);
            // console.log "Trying to add remote db"
            if (this.remote_db.length > 0 && this.remote_db.startsWith("http")) {
              // console.log "declare remote_db"
              return window.remote_db = new PouchDB(this.remote_db);
            } else {

            }
          } else {

          }
        } else {
          //console.warn "Syncing disabled"
          // console.error "remote_db not defined, even though sync is enabled"
          // console.error "Remote db doesn't appear to be a URL"

          // console.log "Found remote db"
          return true;
        }
      } else {
        // console.warn "Sync setting null, set to 'false'"
        return localStorage.setItem(this.sync_key, "false");
      }
    }

    constructor(local_db) {
      this.local_db = local_db;
      this.init();
    }

  };

  // todo: DRY this up
  // todo: logging
  // todo: recurring to be handled in App.svelte, but interval is tracked here
  // todo: block sync if in Edit Mode

  // scheduling shoudl be done outside of here?
  PouchDb.prototype.local_db = void 0;

  PouchDb.prototype.remote_db = void 0;

  PouchDb.prototype.sync_pending = false; // in case a sync is running and another one needs to happen again

  PouchDb.prototype.sync_db = false;

  PouchDb.prototype.sync_interval_minutes = 5;

  PouchDb.prototype.is_syncing = false;

  PouchDb.prototype.sync_store = void 0;

  PouchDb.prototype.notifier = void 0;

  PouchDb.prototype.last_sync_key = "radar_last_sync_attempt";

  PouchDb.prototype.sync_key = "radar_sync";

  PouchDb.prototype.remote_db_key = "radar_remote_db";

  return PouchDb;

}).call(this);

module.exports = PouchDb;
